/**
 * @class Random
 * see: https://ics.media/entry/11292
 */


/**
 * 
 * @static
 * @param {number} min  
 * @param {number} max  
 * @return {number} 
 */
export function random(min = 0, max = 1) {
	let random = Math.random();

	if (min > max) {
		let num = min;
		min = max;
		max = num;
	}
	return random * (max - min) + min;
};


/**
 * int
 * @static
 * @param {number} min  
 * @param {number} max  
 * @return {number} 
 */
export function randomInt(min, max) {
	return Math.round(random(min, max));
}


/**
 * randomLow
 * 0.0
 *
 * @export
 * @returns {number} 0.0 - 1.0
 */
export function randomLow() {
	return Math.random() * Math.random();
};


/**
 * randomLow2
 * 0.0randomLow
 *
 * @export
 * @returns {number} 0.0 - 1.0
 */
export function randomLow2() {
	let r = Math.random();
	return r * r;
};



/**
 * randomHight
 * 1.0
 *
 * @export
 * @returns {number} 0.0 - 1.0
 */
export function randomHight() {
	return 1.0 - Math.random() * Math.random();
};


/**
 * randomHight2
 * 1.0randomHight
 *
 * @export
 * @returns {number} 0.0 - 1.0
 */
export function randomHight2() {
	let r = Math.random();
	return 1.0 - r * r;
};


/**
 * randomNormalize
 * (0.5)
 *
 * @export
 * @returns
 */
export function randomNormalize() {
	let r1 = Math.random();
	let r2 = Math.random();
	let value = Math.sqrt(-2.0 * Math.log(r1)) * Math.sin(2.0 * Math.PI * r2);
	// 01
	return (value + 3) / 6;
};


/**
 * randomLinerLow
 * 0.0
 *
 * @export
 * @returns {number} 0.0 - 1.0
 */
export function randomLinerLow() {
	return 1.0 - Math.sqrt(Math.random());
};


/**
 * randomLinerHight
 * 1.0
 *
 * @export
 * @returns {number} 0.0 - 1.0
 */
export function randomLinerHight() {
	return Math.sqrt(Math.random());
};


/**
 * randomLinerCenter
 * (0.5)
 *
 * @export
 * @returns {number} 0.0 - 1.0
 */
export function randomLinerCenter() {
	return (Math.random() + Math.random()) * 0.5;
};
