/**
 * @class Math
 */

/**
 *  ()
 * @static
 * @property PI
 * @type {number}
 */
export const PI = Math.PI;


/**
 *  * 2 ()
 * @static
 * @property TWO_PI
 * @type {number}
 */
export const TWO_PI = PI * 2;


/**
 *  * 2 (1/4)
 * @static
 * @property HARF_PI
 * @type {number}
 */
export const HARF_PI = PI / 2;


/**
 * 
 * @static
 * @property RAD_TO_DEG
 * @type {number}
 */
export const RAD_TO_DEG = 180 / PI;


/**
 * 
 * @static
 * @property DEG_TO_RAD
 * @type {number}
 */
export const DEG_TO_RAD = PI / 180;


/**
 * 2
 * @static
 * @property sqrt2
 * @type {number}
 */
export const sqrt2 = Math.sqrt(2);


/* Convert
-----------------------------------------------------------------*/
/**
 * 
 * @method radToCoord
 * @param  {number} rad 
 * @param  {number} radius 
 * @return {object} x, y
 */
export function radToCoord(rad, radius = 1) {
	return {
		x: Math.cos(rad) * radius,
		y: Math.sin(rad) * radius
	};
};


/**
 * 
 * @method degToCoord
 * @param  {number} deg 
 * @param  {number} radius 
 * @return {object} x, y
 */
export function degToCoord(deg, radius = 1) {
	return radToCoord(degToRad(deg), radius);
};


/**
 * Radian
 * @static
 * @method coordToRad
 * @param  {number} x x
 * @param  {number} y y
 * @return {number} Radian
 */
export function coordToRad(x, y) {
	return Math.atan2(y, x);
};


/**
 * Degrees
 * @static
 * @method coordToDeg
 * @param  {number} x x
 * @param  {number} y y
 * @return {number} Degrees
 */
export function coordToDeg(x, y) {
	return Math.atan2(y, x) * RAD_TO_DEG;
};


/**
 * 
 * @static
 * @method radToDeg
 * @param {number} rad 
 * @return {number} degree
 */
export function radToDeg(rad) {
	return rad * RAD_TO_DEG;
};


/**
 * 
 * @static
 * @method degToRad
 * @param {number} deg 
 * @return {number} radian
 */
export function degToRad(deg) {
	return deg * DEG_TO_RAD;
};


/**
 * 
 * @static
 * @method diagonal
 * @param  {number} x 
 * @param  {number} y 
 * @return {number}
 */
export function diagonal(x, y) {
	return Math.sqrt(Math.pow(x, 2) + Math.pow(y, 2));
};


/**
 * 45
 * @static
 * @method diagonalToSideLength
 * @param  {number} diagonal 
 * @return {number}
 */
export function diagonalToSideLength(diagonal) {
	return diagonal / utils.sqrt2;
}


/* Utilitys
-----------------------------------------------------------------*/
/**
 * 
 * num+1.00.00.0-1.0
 * @static
 * @method sign
 * @param  {number} num
 * @return {number} -1, 0, 1
 */
export function sign(num) {
	if (0 < num) {
		return 1;
	} else if (0 > num) {
		return -1;
	} else {
		return 0;
	}
};


/**
 * 
 * @static
 * @method fract
 * @param  {number} num
 * @return {number}
 */
export function fract(num) {
	return num - Math.floor(num);
};


/**
 * 
 * @static
 * @method clamp
 * @param {number} num 
 * @param {number} min 
 * @param {number} max 
 * @return {number} 
 */
export function clamp(num, min, max) {
	return Math.max(Math.min(num, max), min);
};


/**
 * 
 * @static
 * @method factorial
 * @param {number} num 
 * @return {number}
 */
export function factorial(num) {
	var total = 1;
	while (num) {
		total = total * num;
		num -= 1;
	}
	return total;
};


/**
 * 
 * @static
 * @method inRange
 * @param  {number} val 
 * @param  {number} min 
 * @param  {number} max 
 * @return {boolean}
 */
export function inRange(val, min, max) {
	return val >= Math.min(min, max) && val <= Math.max(min, max);
};


/**
 * 
 * @static
 * @method isIntersect
 * @param  {number} rangeMin1 1
 * @param  {number} rangeMax1 1
 * @param  {number} rangeMin2 2
 * @param  {number} rangeMax2 2
 * @return {boolean}
 */
export function isIntersect(rangeMin1, rangeMax1, rangeMin2, rangeMax2) {
	return Math.max(rangeMin1, rangeMax1) >= Math.min(rangeMin2, rangeMax2) &&
		Math.min(rangeMin1, rangeMax1) <= Math.max(rangeMin2, rangeMax2);
};


/**
 * 
 * @static
 * @method lerp
 * @param  {number} val 
 * @param  {number} min   
 * @param  {number} max   
 * @return {number}       
 */
export function lerp(val, min, max) {
	return (max - min) * val + min;
};


/**
 * 
 * @static
 * @method smoothstep
 * @param  {number} val 
 * @param  {number} min   
 * @param  {number} max   
 * @return {number}       
 */
export function smoothstep(val, min, max) {
	// return clamp((val - min) / (max - min), 0, 1);
	let t = clamp((val - min) / (max - min), 0, 1);
	return t * t * (3 - 2 * t);
};


/**
 * (0.0-1.0)
 * @static
 * @method normalize
 * @param  {number} val 
 * @param  {number} min   
 * @param  {number} max   
 * @return {number}       
 */
export function normalize(val, min, max) {
	return (val - min) / (max - min);
};


/**
 * 
 * valuescorpeA1 - scorpeA2scorpeB1 - scorpeB2
 * @static
 * @method map
 * @param  {number} val   
 * @param  {number} fromMin 
 * @param  {number} fromMax 
 * @param  {number} toMin 
 * @param  {number} toMax 
 * @return {number}         
 */
export function map(val, fromMin, fromMax, toMin, toMax) {
	return lerp(normalize(val, fromMin, fromMax), toMin, toMax);
};
