import * as array from "./array";
import * as is from "./is";


/**
 * @class String
 */


/**
 * zeroPadding 
 * @param  {number} value 
 * @param  {number} digit  (1=1, 2=2)
 * @return {string}
 */
export function zeroPadding(value, digit, isBeyondValue) {
	let _value = "" + value,
		zero = ("" + Math.pow(10, digit)).slice(1);

	if (digit * 10 > value) {
		return (zero + _value).slice(-digit);
	} else {
		return _value;
	}
};


/**
 * id
 * 
 *
 * @static
 * @method createId
 * @param {string} str id : 'id_' 
 * @return {string} id
 */
export const createId = (() => {
	let _count = 0;

	return function (str = "id_") {
		return str + (_count += 1);
	};
})();


/**
 * objectToQuery  ()
 * @static
 * @method objectToQuery
 * @param  {Object} obj 
 * @param  {boolean} isQuestion ? false
 * @return {string}
 */
export function objectToQuery(obj, isQuestion = false) {
	let query = "";
	array.each(obj, (key, val) => {
		if (query) {
			query += "&";
		}

		if (is.isObject(val)) {
			query += key + "=" + val.keys().length;
		} else if (is.isArray(val)) {
			query += key + "=" + val.length;
		} else {
			query += key + "=" + val;
		}
	});

	if (isQuestion) {
		query = + query;
	}

	return query;
};


/**
 * 
 * @static
 * @method removeSpace
 * @param {string} str 
 * @return {string} 
 */
export function removeSpace(str) {
	return str.replace(/\s+/g, "");
};


/**
 * 
 * 
 * @static
 * @method replaceAll
 * @param {string} str 
 * @param {string} del 
 * @param {string} add 
 * @return {string} 
 */
export function replaceAll(str, del, add) {
	add = add ? add : "";
	return str.split(del).join(add);
};


/**
 * toHarfNumber 
 * @param  {string} str 
 * @return {string}
 */
export function toHarfNumber(str) {
	str = str.replace(/-/g, "");
	return str.replace(/[-]/g, (s) => {
		return String.fromCharCode(s.charCodeAt(0) - 0xFEE0);
	});
};
